<template>
    <div class="container">
      <div class="circle"></div>
      <div class="circle"></div>
      <div class="circle"></div>
      <div class="circle"></div>
      <div class="circle"></div>
      <div class="circle"></div>
    </div>
</template>

<script>
import store from "@/store/store";

export default {
  name: "Hypnosis",

  mounted() {
    document.body.style.backgroundColor = '#00b8a9'
    store.state.navigation_bar_time_color = '#ffffff'
  },

  destroyed() {
    document.body.style.backgroundColor = ''
    store.state.navigation_bar_time_color = '#414141'
  },
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.container {
  margin-top: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.circle {
  width: 50px;
  height: 50px;
  background-color: aliceblue;
  border-radius: 50%;
  position: absolute;

  /*执行动画： 动画名 时长 加速后减速 无限播放*/
  animation: pulse 3s ease-in-out infinite;
}

/* 分别为每个圆形设置不同的宽高、不透明度、动画延时时间 */
.circle:nth-child(1) { width: calc(50px * 1); height: calc(50px * 1); opacity: 1; animation-delay: 0.12s; }
.circle:nth-child(2) { width: calc(50px * 2); height: calc(50px * 2); opacity: 0.5; animation-delay: 0.24s; }
.circle:nth-child(3) { width: calc(50px * 3); height: calc(50px * 3); opacity: 0.33; animation-delay: 0.36s; }
.circle:nth-child(4) { width: calc(50px * 4); height: calc(50px * 4); opacity: 0.25; animation-delay: 0.48s; }
.circle:nth-child(5) { width: calc(50px * 5); height: calc(50px * 5); opacity: 0.2; animation-delay: 0.6s; }
.circle:nth-child(6) { width: calc(50px * 6); height: calc(50px * 6); opacity: 0.16; animation-delay: 0.72s; }

/* 定义动画 */
@keyframes pulse {
  0% {transform: scale(1)}
  25% {transform: scale(1.3)}
  50% {transform: scale(0.7)}
  75% {transform: scale(1)}
}

</style>